.imageContainer{
    width: 83px;
    height: 85px;
}
.listImage{
    height: 80px;
}
.listSection{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.listBox{
    margin-top: 50px;
    list-style: none;
    height: 110px;
    width: 80%;
    border: 2px solid black;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.listPara{
    width: 352px;
    text-align: justify;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.buttonsContainer{
    display: flex;
    align-items: center;
    gap: 10px;
}
.listBtn{
    padding: 1px 11px;
    font-size: 22px;
}
.listRemove{
    font-size: 17px;
    padding: 4px 7px;
    margin-bottom: 6px;
}
.buyBtn{
    margin: 10px 10px;
    padding: 10px;
    font-size: 12px;
    text-decoration: none;
    color: whitesmoke;
    background-color: black;
    border-radius: 10px;
    margin-top: 10px;
}
.backBtn{
    padding: 12px;
    position: relative;
    top: 16px;
    left: 6px;
    margin-bottom: 20px;
    border: none;
    border-radius: 20px;
    text-decoration: none;
    background-color: black;
    color: whitesmoke;
}
.allBtn{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 14px;
}
.priceContainer{
    margin-top: 10px;
}

@media (min-width: 250px) and (max-width: 290px){
.listPara{
    width: 100px;
}
}
@media (min-width: 250px) and (max-width: 500px) {
    .listBox{
        flex-direction: column;
        height: 334px;
    }
    .listPara{
        width: 135px;
    }
    .imageContainer{
        height: 110px;
    }
    .listImage{
        height: 110px;
    }
}
@media (min-width: 501px) and (max-width: 1070px){
    .listBox{
        width: 98%;
    }
    .listBtn{
        gap: 20px;
    }
    
}

@media (min-width: 501px) and (max-width: 600px){
    .listPara{
        width: 91px;
    }
    .imageContainer{
        width: 53px;
    }
    .priceContainer{
        width: 51px;
    }
}

@media (min-width: 601px) and (max-width: 750px) {
    .listPara{
        width: 200px;
    }
}
