.header{
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 75px;
    position: fixed;
    width: 100%;
    top: 0;
}
.home-link, .cart-link{
    text-decoration: none;
    color: white;
}
.cart-link{
    margin-right: 20px;
    font-size: 29px;
}
.home-link{
    margin-left: 20px;
    font-size: 21px;
}

@media (min-width: 250px) and (max-width: 500px) {
.home-link{
    font-size: 13px;
}
}