.Products-section{
    display: flex;
    justify-content: center;
    margin-top: 72px;
}
.cartSection{
    margin-top: 72px;
    margin-bottom: 20px;
}
.Product-Box{
    height: fit-content;
    width: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid black;
    border-radius: 10px;
}

ol{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 50px;
    margin-top: 30px;
    margin-bottom: 30px;
}
.image-container{
    width: 147px;
    height: 194px;
}
.product-image{
    height: 178px;
    margin-top: 10px;
    overflow: hidden;
}
.product-title{
    font-size: 16px;
    text-align: justify;
    color: black;
    text-decoration: none;
    width: 166px;
}
.price{
    margin-top: 10px;
    margin-bottom: 10px;
}
.btn-container{
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 10px;
}
.btn{
    padding: 5px;
    border: 1px solid back;
    background-color: transparent;
    border-radius: 20px;
    cursor: pointer;
    width: 72px;
}

.cart-btn{
    margin-left: 10px;
    background-color: orange;
    color: black;
    border: 1px solid orange;
    margin-right: 25px;
}
.wishlist-btn{
    margin-right: 10px;
    border: 1px solid black;
}

.wishlist-btn:hover{
    background-color: orange;
    border: 1px solid orange;
    color: black;
    transition: 1s;
}
.cart-btn:hover{
    background-color: transparent;
    color: black;
    border: 1px solid black;
    transition: 1s;
}



@media (min-width: 250px) and (max-width: 506px) {
    ol{
        display: grid;
        grid-template-columns: 1fr;
    }
}
@media (min-width: 507px) and (max-width: 750px) {
    ol{
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
}
@media (min-width: 751px) and (max-width: 1000px) {
    ol{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
    }
}